export default {
 
  data () {
    return {
      list: [],
      search_key: '', // 搜索关键词
      page: 1,
      isFinish: false,
      allLoaded: false // 分页加载
    }
  },
  onLoad () {
    this.getData()
    uni.showLoading({
        title: '加载中',
    	mask:"true"
    });
  },
  
  methods: {
	  // 俱乐部会员统计
	  CityClubMember(item){
		  // city: item.name, type: item.type
		  uni.navigateTo({
			url:'../../pagesThree/CityClubMember/CityClubMember?city=' + item.name + '&type=' + item.type
		  })
	  },
    getData () {
      this.$api.detion_report().then(res => {
        uni.hideLoading();
        if (res.data.code === '200') {
          this.list = res.data.data
          this.isFinish = true
        }
      })
    }
  }
}
